<template>
    <div class="login-wrap">
        <div class="ms-login">
            <div class="ms-title">在线考试管理系统</div>
            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="0px" class="ms-content">
                <el-form-item prop="username">
                    <el-input v-model="ruleForm.username" placeholder="用户名">
                        <el-button slot="prepend" icon="el-icon-lx-people"></el-button>
                    </el-input>
                </el-form-item>
                <el-form-item prop="password">
                    <el-input type="password" placeholder="密码" v-model="ruleForm.password" @keyup.enter.native="submitForm('ruleForm')">
                        <el-button slot="prepend" icon="el-icon-lx-lock"></el-button>
                    </el-input>
                </el-form-item>
                <div class="login-btn">
                    <el-button type="primary" @click="submitForm('ruleForm')">登 录</el-button>
                </div>
                <!-- <p class="login-tips">Tips : 用户名和密码随便填。</p> -->
            </el-form>
        </div>
    </div>
</template>

<script>
// import axios from '../../axios';
import axios from '../../axios'
// import api from "@/api/index.js";
    export default {
        data: function(){
            return {
                imgSrc:'',
                ruleForm: {
                    username: '',
                    password: '',
                },
                rules: {
                    username: [
                        { required: true, message: '请输入用户名', trigger: 'blur' }
                    ],
                    password: [
                        { required: true, message: '请输入密码', trigger: 'blur' }
                    ],
                    imgCode: [
                        { required: true, message: '请输入验证码', trigger: 'blur' }
                    ]
                },
                items: [],
            }
        },
        created(){
            // this.huoCode();
        },
        methods: {
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        // localStorage.setItem('ms_username',this.ruleForm.username);
                        // this.$router.push('/');
                        this.getLogin();
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            aae(array, depth=3){
                let result = [];
                array.forEach (item => {
                    let d = depth;
                    if(Array.isArray(item) && d > 0){
                        result.push(...(this.aae(item, --d)))
                    } else {
                        result.push(
                            {
                                index:item.index,
                                aa:111
                            }
                        );
                        // result.push();
                    }
                })
                return result;
            },
            // 登录
            async getLogin(){
                let data = await axios.post('/api/web/login',{userName:this.ruleForm.username,password:this.ruleForm.password})
                console.log(data)
                if(data.data.status==1){
                    // ms_username
                    localStorage.setItem('ms_username',data.data.rows.userName)
                    localStorage.setItem('BXWToken',data.data.rows.token)
                    this.$router.push('/home')
                }else{
                    this.$message.error(data.data.message);
                }
            },
            //生成guid
            newGuid(){
                this.uid='';
                for (var i = 1; i <= 32; i++){
                  var n = Math.floor(Math.random()*16.0).toString(16);
                  this.uid+=   n;
                  if((i==8)||(i==12)||(i==16)||(i==20))
                    this.uid+= "-";
                }
                return this.uid;    
            },
            
        }
    }
</script>

<style>
html,body{height: 100%;}
#app{height: 100%;}
    .login-wrap{
        position: relative;
        width:100%;
        height:100%;
        background-image: url(../../assets/img/login-bg.jpg);
        background-size: 100%;
    }
    .ms-title{
        width:100%;
        line-height: 50px;
        text-align: center;
        font-size:20px;
        color: #409eff;
        border-bottom: 1px solid #ddd;
    }
    .ms-login{
        position: absolute;
        left:50%;
        top:50%;
        width:350px;
        margin:-190px 0 0 -175px;
        border-radius: 5px;
        background: rgba(255,255,255, 0.5);
        overflow: hidden;
    }
    .ms-content{
        padding: 30px 30px;
    }
    .login-btn{
        text-align: center;
    }
    .login-btn button{
        width:100%;
        height:36px;
        margin-bottom: 10px; font-size: 15px;
    }
    .login-tips{
        font-size:12px;
        line-height:30px;
        color:#fff;
    }
    .verifyCode{ width: 200px; position: relative;}
    .verifyCodeInfo{position: absolute; cursor: pointer; background: #eee; width: 75px;height: 32px; text-align: center; right: -89px; top: 0px;}
    .ms-login .el-button>i{font-size: 16px;}
    .verifyCodeInfo img{width: 75px;height: 32px;}
</style>